// ********************
// HERO SECTION
// ********************

#hero__video {
    --video-width: 100vw;

    width: var(--video-width);
    height: calc(0.5625 * var(--video-width));
}

#hero__video-wrapper {
    position: relative;
    z-index: 900;

    > div:first-of-type {
        position: absolute;
        top: -1px;
        right: 0;
        left: 0;
    }


}

#ribbon {
    padding: 0 1.5rem;
    width: 35.625rem;
    height: 2.5rem;
    background-color: #CE0000;
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;

    p {
        color: $white;
    }

}

#close {
    cursor: pointer;
}

#proof {

    img {
        margin: 2.5rem 1.25rem;
        height: 2.5rem;
    }

}

// ********************
// WHO SECTION
// ********************

.tab {
    position: relative;
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.25rem;
    transition: all ease-in-out 0.3s;
    cursor: pointer;

    &:hover {
        background-color: rgba($gray, 0.05);
    }

    &.is-active {
        background-color: $white;
        box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1), 0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);

        &:after {
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            width: 0;
            height: 0;
            border: 1rem solid transparent;
            border-left-color: $white;
            border-right: 0;
            margin-top: -1rem;
            margin-right: -1rem;
        }

    }

    h3 {
        font-weight: 700;
    }

}

.tab__content {
    padding: 2.5rem;
    border: 2px dashed $gold;
    border-radius: 0.5rem;
    background-color: $white;
    box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1), 0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);
}

// ********************
// READY SECTION
// ********************

.cta {
    padding: 2.5rem;
    background-color: rgba($white, 0.1);
    border: 2px dashed $gold;
    border-radius: 0.5rem;

    h3 {
        color: $white;
        text-transform: uppercase;
    }

}

// ********************
// FAQ SECTION
// ********************

.faq {
    cursor: pointer;
    width: 100%;

    > div {
        width: 100%;
        padding: 1.5rem;
        border-bottom: 1px solid rgba($gray, 0.05);
        transition: color ease-in-out 0.3s;

        :hover {
            color: $gold;
        }

    }

    h4 {
        font-size: 1.25rem;
        font-weight: 500;
    }

    p {
        padding: 1.25rem;
        background-color: rgba($gray, 0.05);
    }

}