// ********************
// BASE (GLOBALS)
// ********************

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Avenir', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    overflow-x: hidden;
}

main {

    @media(max-width: map-get($breakpoints-down, 'small')) {
        margin-top: 108px;
    }

    @media(min-width: map-get($breakpoints-up, 'large')) {
        margin-top: 157px;
    }

}