// ********************
// UTILITIES
// ********************

// RESPONSIVE UTILITY CLASSES
.is-hidden-for-mobile {
    @include breakpointsDown(medium) {
        display: none;
    }
}

.is-hidden-for-desktop {
    @include breakpointsUp(medium) {
        display: none;
    }
}

.is-hidden {
    display: none;
}

// ********************
// MARGINS
// ********************

// MARGIN RIGHT
.mr-4 {
    margin-right: 0.25rem;
}

.mr-8 {
    margin-right: 0.5rem;
}

.mr-16 {
    margin-right: 1rem;
}

// MARGIN BOTTOM
.mb-4 {
    margin-bottom: 0.25rem;
}

.mb-8 {
    margin-bottom: 0.5rem;
}

.mb-10 {
    margin-bottom: 0.625rem;
}

.mb-16 {
    margin-bottom: 1rem;
}

.mb-20 {
    margin-bottom: 1.25rem;
}

.mb-40 {
    margin-bottom: 2.5rem;
}

.mb-80 {
    margin-bottom: 5rem;
}

.my-80 {
    margin: 5rem 0;
}

.py-40 {
    padding: 2.5rem 0;
}

.py-80 {
    padding: 5rem 0;
}

.py-120 {
    padding: 7.5rem 0;
}

// ********************
// SHADOWS
// ********************

.shadow-sm {
    box-shadow: 0px 1px 2px 0px rgba($color: #000000, $alpha: 0.05);
}

.shadow-med {
    box-shadow: 0px 1px 3px 0px rgba($color: #000000, $alpha: 0.1), 0px 1px 2px 0px rgba($color: #000000, $alpha: 0.06);
}

.shadow-lg {
    box-shadow: 0px 10px 15px -3px rgba($color: #000000, $alpha: 0.1), 0px 2px 4px -1px rgba($color: #000000, $alpha: 0.06);
}

.shadow-xl {
    box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1), 0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);
}

.shadow-2xl {
    box-shadow: 0px 25px 50px -12px rgba($color: #000000, $alpha: 0.25);
}