// ********************
// CAROUSEL
// ********************

#slide-wrapper {
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
    -ms-overflow-style: none;
    overflow: auto;
    position: relative;
    transition: all ease-in-out 0.3s;
    padding: 2.5rem 0;

    &::-webkit-scrollbar {
        display: none;
    }

    > div {
        scroll-snap-align: center;
    }

}

.slide {
    width: 100%;
    flex-shrink: 0;
}

#carousel__bottom {
    padding: 1rem;
    flex: 1 1 0%;

    span {
        width: 0.75rem;
        height: 0.75rem;   
        margin-left: 0.25rem;
        margin-right: 0.25rem;
        border-radius: 50%;
        background-color: $gray;
        opacity: 0.1;

        &.is-active {
            opacity: 1.0;    
        }

    }

}

.carousel__btn {
    margin: 0 1rem;
    border-radius: 50%;
    color: $gray;
    display: none;
}

.thumbnail {
    width: 40rem;
    height: 22.5rem;
}