// ********************
// CALLOUTS
// ********************

.callout {

    margin-left: auto;
    margin-right: auto;
    max-width: 48.125rem;
    padding: 5rem;
    border-radius: 0.75rem;

    &.is-white {
        background-color: $white;
        border: 2px dashed $gold;
    }

    &.is-dark {
        background-color: $gray;
        color: rgba($white, 0.8);

        > h2 {
            color: $white;
        }

        > hr {
            opacity: 0.5;
        }

        strong {
            color: rgba($white, 1.0);
        }

    }

    &.is-beige {
        background-color: rgba($beige, 0.5);
    }

    &.is-transparent {
        background-color: rgba($gray, 0.5);
        border: 2px dashed rgba($beige, 0.7);
    }

    h2 {
        text-transform: unset;
    }

}