// ********************
// FONT SIZES
// ********************

.text-xs {
    font-size: 0.75rem;
    line-height: 1rem;
}

.text-sm {
    font-size: 0.875rem;
    line-height: 1.25rem;
}

.text-base {
    font-size: 1rem;
    line-height: 1.5rem;
}

.text-lg {
    font-size: 1.125rem;
    line-height: 1.75rem;
}

.text-xl {
    font-size: 1.25rem;
    line-height: 1.75rem;
}

.text-2xl {
    font-size: 1.5rem;
    line-height: 2rem;
}

.text-3xl {
    font-size: 1.875rem;
    line-height: 2.25rem;
}

.text-4xl {
    font-size: 2.25rem;
    line-height: 2.5rem;
}

.text-5xl {
    font-size: 3rem;
    line-height: 1;
}

.text-6xl {
    font-size: 3.75rem;
    line-height: 1;
}

.text-7xl {
    font-size: 4.5rem;
    line-height: 1;
}

.text-8xl {
    font-size: 6rem;
    line-height: 1;
}

.text-9xl {
    font-size: 8rem;
    line-height: 1;
}

// ********************
// TEXT TRACKING
// ********************

.tracking-tighter {
    letter-spacing: -0.05em;
}

.tracking-tight {
    letter-spacing: -0.025em;
}

.tracking-normal {
    letter-spacing: 0em;
}

.tracking-wide {
    letter-spacing: 0.025em;
}

.tracking-wider {
    letter-spacing: 0.05em;
}

.tracking-widest {
    letter-spacing: 0.1em;
}

.text-center {
    text-align: center;
}

// ********************
// HEADINGS
// ********************

h1 {

    &.sr-only {
        position: absolute;
        width: 1px;
        height: 1px;
        padding: 0;
        margin: -1px;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        white-space: nowrap;
        border-width: 0;
    }

}

h2 {
    color: $gray;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: -0.025em;

    > .gold-text {
        font-weight: 700;
        color: $gold;
    }

    > .beige-text {
        font-weight: 700;
        color: $beige;
    }


}

h4 {
    color: $gray;
    font-weight: 700;
    font-size: 1.25rem;
}

// ********************
// PROSE
// ********************

.prose {
    margin: 0 auto;
    max-width: 65ch;
    color: $gray;
    font-size: 1rem;
    line-height: 1.75;
}

.prose {
    @media(max-width: map-get($breakpoints-down, 'small')) {
        font-size: 0.875rem;
        line-height: 1.71;
    }
    
    @media(min-width: map-get($breakpoints-down, 'medium')) {
        font-size: 1rem;
        line-height: 1.75;
    }
    
    @media(min-width: map-get($breakpoints-down, 'large')) {
        font-size: 1.125rem;
        line-height: 1.78;
    }
    
    @media(min-width: map-get($breakpoints-down, 'xl')) {
        font-size: 1.5rem;
        line-height: 1.67;
    }
}

.prose p {
    margin: 1.25em 0;

    @media(max-width: map-get($breakpoints-down, 'small')) {
        margin: 1.14em 0;
    }

    @media(min-width: map-get($breakpoints-down, 'medium')) {
        margin: 1.25em 0;
    }

    @media(min-width: map-get($breakpoints-down, 'large')) {
        margin: 1.33em 0;
    }

}

.highlight {
    background-image: linear-gradient(to right, rgba($yellow, 1.0), rgba($yellow, 0.5));
    border-radius: 0.375rem 0.188rem 0.375rem 0.188rem;
    padding: 0 0.375rem;
}

.checklist__item {
    font-size: 1rem;
    line-height: 1.75;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-left: 2.5rem;
    background-image: url('/img/check-icon.svg');
    background-size: 1.5rem;
    background-repeat: no-repeat;
    background-position: left 0.5rem;

    &.is-small {
        font-size: 0.875rem;
        line-height: 1.71;
        opacity: 0.8;
    }

}