// ********************
// SECTIONS
// ********************

.section {
    position: relative;

    &.is-gray {
        background-color: rgba($gray, 0.05);
    }

    &.is-dark-gray {
        background-color: $gray;

        h2 {
            color: $white;
        }

        p {
            color: rgba($white, 0.8);
        }
    }

}

// ********************
// CONTAINERS
// ********************

.container {

    margin: 0 auto;

    @media(max-width: map-get($breakpoints-down, 'small')) {
        max-width: 40rem; // 640px
        padding: 0 2rem;
    }

    @media(min-width: map-get($breakpoints-down, 'medium')) {
        max-width: 48rem; // 768px
        padding: 0 3rem;
    }

    @media(min-width: map-get($breakpoints-down, 'large')) {
        max-width: 64rem; // 1024px
        padding: 0 4rem;
    }

    @media(min-width: map-get($breakpoints-down, 'xl')) {
        max-width: 80rem; // 1280px
        padding: 0 5rem;
    }

    @media(min-width: map-get($breakpoints-down, '2xl')) {
        max-width: 96rem; // 1536px
        padding: 0 6rem;
    }

}

