header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    background-color: $gray;

    hr {
        opacity: 0.5;
    }

}

.header__top {
    padding: 1.5rem;
}

nav {
    padding: 0.75rem;

    a {
        opacity: 0.5;
        color: $white;
        margin: 0 2.5rem;
        will-change: opacity;
        transition: opacity 0.3s ease-in-out;

        &:hover {
            opacity: 1.0;
        }

    }

}

#brand {

    > img {
        width: auto;
        height: 3.75rem;
    }

}